<template>
  <section>
    <div class="buttonsContainer">
      <button
        v-for="item in tabs"
        :key="item"
        :class="['tabButton', { active: currentTabAsComponent === item}]"
        @click="changeTab(item)"
      >
        {{ item }}
      </button>
    </div>
    <component :is="currentTabAsComponent" style="padding-top:50px"/>
  </section>
</template>

<script>
  import Quote from '../../components/__LAB_examples__/Quote'
  import Animation from '../../components/__LAB_examples__/Animation'
  import Animation2 from '../../components/__LAB_examples__/Animation2'
  import ClickOutside from '../../components/__LAB_examples__/ClickOutside'
  import ScrollOpacity from '../../components/__LAB_examples__/ScrollOpacity'
  import LabMain from '../../components/__LAB_examples__/LabMain'
  import CountDown from '../../components/__LAB_examples__/CountDown'
  import ScrollAppearElement from '../../components/__LAB_examples__/ScrollAppearElement'
  export default {
    components: {
      LabMain, CountDown, ScrollAppearElement, ScrollOpacity,ClickOutside,Animation, Animation2, Quote
    },
    data() {
      return {
        currentTabAsComponent: "LabMain",
        tabs: ["LabMain","CountDown", 'ScrollAppearElement', 'ScrollOpacity','ClickOutside','Animation','Animation2','Quote'],
      };
    },
    methods:{
      changeTab(item){
        this.currentTabAsComponent = item;
      },
    },
  };
</script>

<style scoped>

  .buttonsContainer{
    display: flex;
    justify-content: center;
  }
  @media only screen and (max-width: 768px) {

    .buttonsContainer{
      display: flex;
      flex-wrap: wrap;
    }

  }

</style>
